/* Header */

.header {
	

	
	padding: 40px 0;
	min-height: 695px;
    display: flex;
    

	background-color: var(--header-bg);
	background-image: url('./../../img/header-bg.png');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center;

	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}

.stack {
	margin-top: 3rem;
	
}
img{
	transition: 1s ease;
	max-height: 100px;
	max-width: 100px;
	padding: 1rem;
}

.btn {
	
	font-size: 2rem;
	color: var(--accent);}
img:hover{
	-webkit-transform: scale(0.8);
	-ms-transform: scale(0.8);
	transform: scale(0.8);
	transition: 1s ease;
}